import VueRouter from 'vue-router';
import Vue from 'vue';

const files = require.context('./module', false, /\.js$/);
let routes = [];
files.keys().forEach((key) => {
    routes = routes.concat(files(key).default || []);
});
Vue.use(VueRouter);
const router = new VueRouter({
    mode: 'history',
    routes,
});

router.onError((err) => {
    console.log(16, err);
});
export default router;


