export default [
    {
        path: '/login',
        name: 'Login',
        component: () => import('@/views/login/Layout'),
        meta: { title: '登陆' },
        children: [
            {
                path: '/login/user',
                name: 'LoginUser',
                component: () => import('@/views/login/user/Layout'),
            },
        ],
    },
];


